import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Context from '../../components/Context/Context';

import styles from './home.less';

const data=[
    {
        fid:1,
        title:'',
        context:[
            {
                id:1,
                subTitle:'HTML',
                content:'HTML HTML  WEB  W3Cschool  HTML  HTML HTML ',
            },
            {
                id:2,
                subTitle:'HTML5',
                content:'HTML 5  HTMLHTML5  HTML5  W3Cschool  HTML 5  HTML5 ',
            },
            {
                id:3,
                subTitle:'CSS',
                content:'CSS Cascading Style Sheets CSS  CSS  XHTML CSS  CSS ',
            },
        ]
    },
    {
        fid:2,
        title:'',
        context:[
            {
                id:1,
                subTitle:'HTML',
                content:'HTML HTML  WEB  W3Cschool  HTML  HTML HTML ',
            },
            {
                id:2,
                subTitle:'HTML5',
                content:'HTML 5  HTMLHTML5  HTML5  W3Cschool  HTML 5  HTML5 ',
            },
            {
                id:3,
                subTitle:'CSS',
                content:'CSS Cascading Style Sheets CSS  CSS  XHTML CSS  CSS ',
            },
        ]
    },
];

class index extends Component {
    render() {
        return (
            <div >
                <ul className={styles.navBox}>
                    <li>
                        <Link to='/home'></Link>
                    </li>
                    <li>
                        <Link to='/program'></Link>
                    </li>
                    <li>
                        <Link to='/document'></Link>
                    </li>
                    <li>
                        <Link to='/special'></Link>
                    </li>
                    <li>
                        <Link to='/vip'></Link>
                    </li>
                </ul>
                <h2 className={styles.title}>
                    <i className={styles.icon}></i>
                    <span></span>
                </h2>
                {
                    data.map((v,k)=><Context key={k} {...v} />)
                }
            </div>
        );
    }
}

export default index;